import Vue from 'vue'
import { post, get, wx_pay, showToast,reBuildUrl } from '../../tools/utils.js'
const state = {
	content:[],
	upimgUrl:'user/upimg',
	upIDCardUrl:'user/upidcard'
}

const getters = {
    content(state) {
    	return state.content
    },
	upimgUrl(state) {
		return reBuildUrl(state.upimgUrl);
	},
	upIDCardUrl(state) {
		return reBuildUrl('user/upidcard');
	}
	
}

const mutations = {
	

}
